* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

.search-form {
  font: inherit;
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.search-input {
  border: none;
  outline: none;
  padding: 5px 5px 5px 15px;
  font-size: 16px;
}

.btn {
  cursor: pointer;
  background-color: #d3d3d3;
  border: none;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: background-color .25s linear;
  display: flex;
}

.btn:hover, .btn:focus {
  background-color: #a9a9a9;
}

.load-more {
  cursor: pointer;
  color: #000;
  background-color: rgba(220, 220, 220, .827);
  border: none;
  border-radius: 8px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 50%;
  padding: 10px;
  transition: background-color .25s linear;
  transform: translateX(-50%);
}

.load-more.is-hidden {
  display: none;
}

.load-more:hover, .load-more:focus {
  background-color: rgba(220, 220, 220, .46);
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.photo-card {
  width: calc(33.3333% - 20px);
  border-radius: 8px;
  margin: 10px;
  overflow: hidden;
  box-shadow: 0 0 5px 2px rgba(0, 4, 56, .5);
}

.photo-img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

